<template>
  <div>
    <el-card>
      <span>选择学期： </span>
      <el-select v-model="value"
                 @change="change(value)">
        <el-option v-for="item in options"
                   :key="item.value"
                   :label="item.label"
                   :value="item.value">
        </el-option>
      </el-select>
      <el-divider></el-divider>
      <el-row :gutter="20">
        <el-col :span="5">
          <div>
            <div id="eduratio">
            </div>
          </div>

        </el-col>
        <el-col :span="7">
          <div>
            <div id="sexratio">
            </div>
          </div>
        </el-col>
        <el-col :span="10">
          <div id="comments"></div>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="12">
          <div id="daytime"></div>
        </el-col>
        <el-col :span="12">
          <div id="weektime"></div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Group',
  data () {
    return {
      options: [{
        value: '2019年秋',
        label: '2019年秋'
      }, {
        value: '2019年春',
        label: '2019年春'
      }, {
        value: '2018年秋',
        label: '2018年秋'
      }, {
        value: '2018年春',
        label: '2018年春'
      }, {
        value: '2017年秋',
        label: '2017年秋'
      }, {
        value: '2017年春',
        label: '2017年春'
      }
      ],
      value: '2019年秋'
    }
  },
  methods: {
    //换学期
    change (value) {
      this.getEduRatio()
      this.getSexRatio()
      this.getComments(value)
      this.getDayTime(value)
      this.getWeekTime(value)
    },
    //学历比重图
    getEduRatio () {
      var myChart = this.$echarts.init(document.getElementById('eduratio'));
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/group/degree',
        method: 'get',
      }).then(res => {
        console.log(res);
        console.log(res.data.data);
        var people = []
        for (var i in res.data.data) {
          people.push(res.data.data[i])
        }
        console.log(people);
        //装载数据
        var option = {
          title: {
            text: '教师学历比重',
            x: 'center'
          },
          series: [
            {
              hoverAnimation: false,
              cursor: 'default',
              type: 'pie',
              radius: '40%', // 设置环形饼状图， 第一个百分数设置内圈大小，第二个百分数设置外圈大小
              center: ['50%', '50%'], // 设置饼状图位置，第一个百分数调水平位置，第二个百分数调垂直位置
              label: {
                normal: {
                  formatter: '{b}:{c}人' + '\n\r' + '({d}%)',
                  show: true,
                  position: 'left'
                },
              },
              data: [
                { value: people[0], name: '本科' },
                { value: people[3], name: '研究生' },
                { value: people[2], name: '博士' },
                { value: people[1], name: '其他' },
              ],
              itemStyle: {
                // normal: {
                //   borderColor: '#fff',
                //   borderWidth: '2'
                // },
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(128, 128, 128, 0.5)'
                }
              },
              // animation: false,
              color: ['#6bd7d2', '#31c2da', '#409EFF', '#988ae0']
            }
          ]
        }
        myChart.setOption(option);
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })
    },

    //性别比重图
    getSexRatio () {
      var myChart = this.$echarts.init(document.getElementById('sexratio'));
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/group/sex',
        method: 'get',
      }).then(res => {
        console.log(res);
        console.log(res.data.data);
        //装载数据
        var option = {
          title: {
            text: '教师性别比重',
            x: 'center'
          },
          series: [
            {
              hoverAnimation: false,
              cursor: 'default',
              type: 'pie',
              radius: '40%', // 设置环形饼状图， 第一个百分数设置内圈大小，第二个百分数设置外圈大小
              center: ['50%', '50%'], // 设置饼状图位置，第一个百分数调水平位置，第二个百分数调垂直位置
              label: {
                normal: {
                  formatter: '{b}:{c}人' + '\n\r' + '({d}%)',
                  show: true,
                  position: 'left'
                },
              },
              data: [
                { value: res.data.data.m, name: '男' },
                { value: res.data.data.w, name: '女' },
              ],
              itemStyle: {
                // normal: {
                //   borderColor: '#fff',
                //   borderWidth: '1'
                // },
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(128, 128, 128, 0.5)'
                }
              },
              // animation: false,
              color: ['#409EFF', '#ed8a8a']
            }
          ]
        }
        myChart.setOption(option);
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })
    },



    //周教师评论数变化趋势
    getComments (val) {
      var myChart = this.$echarts.init(document.getElementById('comments'));
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/group/diss',
        params: {
          semester: val
        },
        method: 'get',
      }).then(res => {
        console.log(res);
        var xArr = []
        var yArr = []
        for (var i in res.data.data) {
          xArr.push(i)
          yArr.push(res.data.data[i])
        }
        console.log(xArr, yArr);
        //装载数据
        var option = {
          title: {
            text: '教师周评论数',
            x: 'center'
          },
          tooltip: {
            trigger: 'axis',
            formatter: '第{b}周评论数</br>{c}条'
          },
          xAxis: {
            name: '周数',
            type: 'category',
            data: xArr
          },
          yAxis: {
            name: '评论数',
            type: 'value'
          },
          series: [{
            data: yArr,
            type: 'line',
            smooth: true,
            color: ['#409EFF']
          }]
        }
        myChart.setOption(option);
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })
    },

    //根据学期查询周在线天数
    getDayTime (val) {
      var myChart = this.$echarts.init(document.getElementById('daytime'));
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/group/day',
        params: {
          semester: val
        },
        method: 'get',
      }).then(res => {
        console.log(res);
        var xArr = []
        var yArr = []
        for (var i in res.data.data) {
          xArr.push(i)
          yArr.push(res.data.data[i])
        }
        console.log(xArr, yArr);
        //装载数据
        var option = {
          title: {
            text: '学生周在线天数',
            x: 'center'
          },
          tooltip: {
            trigger: 'axis',
            formatter: '第{b}周在线天数</br>{c}天'
          },
          xAxis: {
            name: '周数',
            type: 'category',
            data: xArr
          },
          yAxis: {
            name: '天',
            type: 'value'
          },
          series: [{
            data: yArr,
            type: 'line',
            smooth: true,
            color: ['#409EFF']
          }]
        }
        myChart.setOption(option);
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })
    },

    //根据学期查询周在线平均时长
    getWeekTime (val) {
      var myChart = this.$echarts.init(document.getElementById('weektime'));
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/group/week',
        params: {
          semester: val
        },
        method: 'get',
      }).then(res => {
        console.log(res);
        var xArr = []
        var yArr = []
        for (var i in res.data.data) {
          xArr.push(i)
          yArr.push(res.data.data[i])
        }
        console.log(xArr, yArr);
        //装载数据
        var option = {
          title: {
            text: '学生周在线时长',
            x: 'center'
          },
          tooltip: {
            trigger: 'axis',
            formatter: '第{b}周在线时长</br>{c}分钟'
          },
          xAxis: {
            name: '周数',
            type: 'category',
            data: xArr
          },
          yAxis: {
            name: '分钟',
            type: 'value'
          },
          series: [{
            data: yArr,
            type: 'line',
            smooth: true,
            color: ['#409EFF']
          }]
        }
        myChart.setOption(option);
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })
    },

  },
  mounted () {
    this.getEduRatio()
    this.getSexRatio()
    this.getComments()
    this.getDayTime()
    this.getWeekTime()
  },
  components: {

  }
}
</script>

<style>
#eduratio {
  width: 300px;
  height: 300px;
}
#sexratio {
  width: 300px;
  height: 300px;
}
#weektime {
  width: 500px;
  height: 300px;
}
#daytime {
  width: 600px;
  height: 300px;
}
#comments {
  width: 500px;
  height: 300px;
}
</style>





